    display: block;
    width: 100%;
    border-bottom-color: black;
    border-bottom-width: 2px;
    position:relative;
    border-bottom-style: dotted;
    text-align:right;
} */



td{
    padding:0
}
</style>
<style scoped>
img{
    max-width: 700px;
}
</style>